import { stringify } from '@angular/compiler/src/util';
import {AfterViewInit, Component, Injectable, OnDestroy, OnInit, ViewChild} from '@angular/core';
import { Router } from '@angular/router';
import { Subscription } from 'rxjs';
import { AuthService } from 'src/app/common/auth.service';
import { DocData } from 'src/app/data/doc-data';
import swal from 'sweetalert';
import { ReportService } from '../generate-report/report.service';
import { XmlViewerService } from '../xml-viewer/xml-viewer.service';
import {MatTableDataSource} from '@angular/material/table';
import {MatPaginator} from '@angular/material/paginator';
import {MatSort} from '@angular/material/sort';
import {ReportHistoryService} from './report-history.service';
import {NotifierService} from '../../notifier.service';
import {FormControl} from '@angular/forms';
import {MomentDateAdapter, MAT_MOMENT_DATE_ADAPTER_OPTIONS} from '@angular/material-moment-adapter';
import {DateAdapter, MAT_DATE_FORMATS, MAT_DATE_LOCALE} from '@angular/material/core';
import {MatDatepicker} from '@angular/material/datepicker';

// Depending on whether rollup is used, moment needs to be imported differently.
// Since Moment.js doesn't have a default export, we normally need to import using the `* as`
// syntax. However, rollup creates a synthetic default module and we thus need to import it using
// the `default as` syntax.
import * as _moment from 'moment';
// tslint:disable-next-line:no-duplicate-imports
import {default as _rollupMoment, Moment} from 'moment';

const moment = _rollupMoment || _moment;

// See the Moment.js docs for the meaning of these formats:
// https://momentjs.com/docs/#/displaying/format/
export const MY_FORMATS = {
  parse: {
    dateInput: 'MM/YYYY',
  },
  display: {
    dateInput: 'MM/YYYY',
    monthYearLabel: 'MMM YYYY',
    dateA11yLabel: 'LL',
    monthYearA11yLabel: 'MMMM YYYY',
  },
};

export const CBN_FORMAT = {

  parse: {
    dateInput: 'DD/MM/YYYY', // this is how your date will be parsed from Input
  },
  display: {
    dateInput: 'DD/MM/YYYY', // this is how your date will get displayed on the Input
    monthYearLabel: 'MMMM YYYY',
    dateA11yLabel: 'LL',
    monthYearA11yLabel: 'MMMM YYYY'
  }

}

@Component({
  selector: 'app-main',
  templateUrl: './main.page.html',
  styleUrls: ['./main.page.css'],
  providers:[
      // `MomentDateAdapter` can be automatically provided by importing `MomentDateModule` in your
    // application's root module. We provide it at the component level here, due to limitations of
    // our example generation script.
    {
      provide: DateAdapter,
      useClass: MomentDateAdapter,
      deps: [MAT_DATE_LOCALE, MAT_MOMENT_DATE_ADAPTER_OPTIONS]
    },

    {provide: MAT_DATE_FORMATS, useValue: MY_FORMATS},

    { provide: DateAdapter, useClass: MomentDateAdapter, deps: [MAT_DATE_LOCALE] },
{ provide: MAT_DATE_FORMATS, useValue: CBN_FORMAT },
{provide: MAT_DATE_LOCALE, useValue: 'en-GB' }
  ],
})
@Injectable({
  providedIn: 'root'
})
export class MainPage implements OnInit, OnDestroy {

  @ViewChild(MatPaginator) paginator: MatPaginator;
  @ViewChild(MatSort) sort: MatSort;

  selected = 'options';
  username: string;
  reportSelectedDate;
  CBNSelectedDate;
  $user: Subscription;
  $selectedDate: Subscription;
  timeIntervalId: any;
  nowDate: Date;
  isFilesLoading = true;
  isError = false;
  errorMessage = 'An Error occurred';
  reportList: Array<DocData> = [];
  displayedColumns: string[] = ['id', 'ReportDescription', 'Date', 'action'];
  dataSource: MatTableDataSource<any>;
  isDate = false;
  tableLength;

  selectedGroup: string = '';
  constructor(
    private authService: AuthService,
    private notifierService: NotifierService,
    private reportService: ReportService,
    private reportHistoryService: ReportHistoryService,
    private router: Router,
    private readonly xmlViewerService: XmlViewerService
  ) {
  }

  ngOnInit(): void {

    this.timeIntervalId = setInterval(() => {
      this.nowDate = new Date();
    }, 1000);

    this.$user = this.authService.user.subscribe(user => {
      this.username = user?.username;
    });

    this.reportService.selectedDate.subscribe((date) => {
      this.reportSelectedDate = date;

    });
    this.getReportHistory();
    this.reportService.setReportGroup(this.selectedGroup)
    console.log("Get Report Group: ", this.reportService.getReportGroup());

  }

  // ngAfterViewInit(): void {
  //   this.dataSource.paginator = this.paginator;
  //   this.dataSource.sort = this.sort;
  // }



  getReportHistory(): void {
    this.reportHistoryService.getReportHistory().subscribe({
      next: (res) => {
        console.log(res)
        this.dataSource = new MatTableDataSource<any>(res);
        this.tableLength = this.dataSource.data.length;
      }, error: err => {
        this.notifierService.showNotification('Unable to Load History', 'OK', 'error');
      }
    })
  }

  getReportGroup() : string {
    return this.selectedGroup;
  }

  postReportDate(reportSelectedDate: any) : void {
    this.reportHistoryService.postReportDate(reportSelectedDate).subscribe({
      next: (res) => {
        this.notifierService.showNotification(`Selected ${reportSelectedDate}`, 'OK', 'success' );
      }, error: err => {
        this.notifierService.showNotification('Unable to select date', 'OK', 'error');
      }
    })
  }


  postCbnDate(CBNSelectedDate: any) : void{
    this.reportHistoryService.postCbnDate(CBNSelectedDate).subscribe({
      next: (res)=> {
        this.notifierService.showNotification(`Selected ${CBNSelectedDate}`, 'OK', 'success' );
      }, error: err=>{

        this.notifierService.showNotification('Unable to select date', 'OK', 'error');

      }

    })

  }



  deleteReportHistory(id: any) : void {
    this.reportHistoryService.deleteReportHistory(id).subscribe({
      next: value => {
        this.notifierService.showNotification('Deleted Succesfully', 'OK', 'success');
        this.getReportHistory();
      },  error: err => {
        this.notifierService.showNotification('Unable to delete', 'OK', 'error');
      }
    })
  }

  changeGroup() {
    this.reportService.setReportGroup(this.selectedGroup)
    console.log("Selected Group: ", this.selectedGroup);

    // // const report_group = 'my_report_group';
    this.reportService.setReportGroup(this.selectedGroup).subscribe((response) => {
      console.log(response);
    });
  }

  fetchDocument() {
    this.isFilesLoading = true;
    this.isError = false;
    this.xmlViewerService.getDocuments().subscribe(() => {
      this.isFilesLoading = false;
    }, err => {
      this.isFilesLoading = false;
      this.isError = true;
      swal({
        title: 'Failed to load Report history',
        icon: 'error',
      });
    });
  }

  selectDate(value: string) {
    console.log(this.reportSelectedDate)
    this.reportSelectedDate = value;
    this.reportService.setReportDate(value.toString())
    this.postReportDate(value);
    console.log(value);
    // .subscribe(_ => {}, err => {
    //   swa({
    //     title: 'Couldn\'t set date',
    //     text: `An error occurred while setting date`,
    //     icon: 'error',
    //   });
    // })
  }

  selectCbnDate(value: string){
    this.CBNSelectedDate = value;
    console.log(this.CBNSelectedDate)
    this.postCbnDate(value);
  }


  deleteFile (id: number, name: string) {
    this.xmlViewerService.deleteDocument(id).subscribe(() => {

    }, err => {
      console.log(err)
        swal({
        title: 'Error',
        text: `${name} failed to delete`,
        icon: 'error'
      });
    });
  }

  saveFile (id: number) {
    this.xmlViewerService.saveDocument(id);
  }

  ngOnDestroy() {
    clearInterval(this.timeIntervalId);
    if(this.$selectedDate) {
      this.$selectedDate.unsubscribe();
    }

    if(this.$user) {
      this.$user.unsubscribe();
    }
  }

  // tslint:disable-next-line:typedef

  getDate() {
    return this.reportSelectedDate;
  }

  applyFilter(event: Event): void {
    const filterValue = (event.target as HTMLInputElement).value;
    this.dataSource.filter = filterValue.trim().toLowerCase();
    if (this.dataSource.paginator) {
      this.dataSource.paginator.firstPage();
    }
  }

  date = new FormControl(moment());

  chosenYearHandler(normalizedYear: Moment) {
    const ctrlValue = this.date.value;
    ctrlValue.year(normalizedYear.year());
    this.date.setValue(ctrlValue);
  }

  chosenMonthHandler(normalizedMonth: Moment, datepicker: MatDatepicker<Moment>) {
    const ctrlValue = this.date.value;
    ctrlValue.month(normalizedMonth.month());
    this.date.setValue(ctrlValue);
    console.log(this.date.value);
    datepicker.close();
  }
}
